.input {
  padding: 9px 22px;
  border: none;
  display: block;
  width: 100%;
  border-radius: 20px;
  background: #f2f5fa;
  color: #454b58;
  font-size: 13px;
  font-style: normal;
  font-weight: 500;
  border: 1px solid transparent;
}
.input::-moz-placeholder {
  color: #454b58;
}
.input::placeholder {
  color: #454b58;
}
.input:hover {
  box-shadow: 0 0 5px rgb(42, 51, 66);
}
.input:focus {
  border-color: rgb(42, 51, 66);
}
